.cn {
  width: 522px;
  height: 321px;
  margin: 20px;
}
.main {
  background: #fbfbfb;
  border: 1px solid #74b4ff;
  box-sizing: border-box;
  border-radius: 4px;
  font-size: 12px;
  font-family: sans-serif;
  padding: 20px;
}
.top {
  display: flex;
  align-items: center;
}
.top img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;

  margin-right: 20px;
}
.username {
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 359px;
  white-space: nowrap;
}
.bottom {
  background: #ffffff;
  border: 1px solid #e8e8e8;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 10px;
  height: 130px;
  margin-bottom: 10px;
}
.bt {
  color: #808080;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
